@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;1,300;1,400&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
		"Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.mainHeader {
	background-color: #d3d1d1;
	height: 3rem;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
	-webkit-align-items: center;
	        align-items: center;
	grid-gap: 2rem;
	gap: 2rem;
	padding-right: 3rem;
}

.userProfile {
	height: 2rem;
	width: 2rem;
	background-color: red;
	border: 2px solid white;
	border-radius: 50%;
}

.burgerMenu img,
.eyeIcon img {
	height: 1.7rem;
	width: 1.7rem;
}

.mainArea {
	display: grid;
	grid-template-columns: 20% 80%;
	grid-template-rows: 70% 30%;
	background-color: beige;
}

.inputTools {
	background-color: #eff5f8;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	text-align: left;
	padding: 1rem 0;
}

.subComponents img {
	height: 1.5rem;
	width:  1.5rem;
	margin-right: 0.5rem;
	vertical-align: middle;
}

.subComponents {
	margin: 0.4rem 1.5rem;
	font-size: 0.9rem;
	cursor: pointer;
}

.subComponents input,
.subComponents select,
.subComponents textarea,
.subComponents label {
	cursor: pointer;
	padding: 0.2rem;
	margin-right: 0.5rem;
}

.matrixDropdown {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.mainCanvas {
	background-color: #2c7572;
}

/* ===== CANVAS HEADER ===== */

.mainCanvas__Header {
	background: linear-gradient(-45deg, #096295, #03bec4);
	height: 2.5rem;
	font-size: small;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-flex-direction: row;
	        flex-direction: row;
}

.mainCanvas__Header--Items {
	height: 2rem;
	width: 12rem;
	margin-top: 0.5rem;
	text-align: left;
	padding: 0.6rem 0 0.2rem 2rem;
	cursor: pointer;
}

.mainCanvas__Header .first {
	background-color: #d7dde0;
}

.mainCanvas__Header .second {
	background-color: #b7c6cf;
}

/* ===== CANVAS  ===== */

.canvasPageParent {
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	margin: -1.5rem 0 0 1rem;
}

.canvasPage {
	background-color: #ebe5e5;
	box-shadow: 1px 2px 6px 1px #9c9999;
	height: 88%;
	width: 80%;
}

.rightArrow,
.leftArrow {
	height: 4rem;
	width: 3rem;
	cursor: pointer;
}

/* === cursor icons ==== */

.canvasTools {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	grid-gap: 1rem;
	gap: 1rem;
	background-color: #e0e4e7;
	padding: 1.5rem 0.5rem;
	border-top-left-radius: 1rem;
	border-bottom-left-radius: 1rem;
}

.canvasTools img {
	width: 1.5rem;
	height: 1.5rem;
}
/* ==== add icon ==== */

.addIcon {
	position: absolute;
	top: 68%;
	right: 4%;
}

.addIcon img {
	height: 4rem;
	width: 4rem;
}

.smallCanvas {
	height: 8rem;
	width: 15rem;
	text-align: left;
	background-color: #ebe5e5;
	box-shadow: 1px 2px 6px 1px #9c9999;
}

.smallCanvas h3 {
	padding: 0.5rem;
}

.secondaryCanvas {
	background-color: #d3d1d1;
	grid-column: 1/3;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	height: 10rem;
	-webkit-align-items: center;
	        align-items: center;
}

.secondaryCanvas img {
	height: 2rem;
	width: 1.8rem;
	margin: 0 1.5rem;
}

